.all-projects-container {
	display: flex;
	flex-wrap: wrap;
	height: auto;
	width: 100%;
}
.all-projects-project {
	width: calc(100% / 3);
	box-sizing: border-box;
	padding: 10px;
	overflow: hidden;
}

@media (max-width: 600px) {
	.all-projects-project {
		width: 100%;
		height: auto;
	}
}
